.cell {
    width: 175px;
    height: 175px;
    border: 2.5px solid black;
    position: initial;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media (max-width: 550px) {
    .cell {
        width: 120px;
        height: 120px;
    }

}

.currentCell {
    background-color: bisque;
}